<template>
  <v-layout row wrap>
    <v-flex xs12 sm6 md4>
      <v-menu
        v-model="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="terms.startDate"
            label="시작날짜"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker locale="ko-KR" v-model="terms.startDate" @input="menu = false"></v-date-picker>
      </v-menu>
    </v-flex>
    <v-flex xs12 sm6 md4>
      <v-menu
        v-model="menu1"
        :close-on-content-click="false"
        :nudge-right="40"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="terms.endDate"
            label="끝날짜"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker locale="ko-KR" v-model="terms.endDate" @input="menu1 = false"></v-date-picker>
      </v-menu>
    </v-flex>
    <v-btn color="success" @click="ac_getCalculate(terms)">검색</v-btn>
    <v-flex xs12>
      <v-btn color="success" align-end @click="saveToExcel(startDate)">엑셀다운</v-btn>
      <v-spacer></v-spacer>
      <v-card xs10 offset-xs1>
      <v-card-title>
        <span class="font-weight-bold title">정산(*취소 환불건은 취소 환불 버튼을 클릭해 주세요!)</span>
        <v-spacer></v-spacer>
        <v-text-field v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="calcuOrderData"
        :search="search"
        :rows-per-page-items="rowsPage"
        id="bb_list_table"
      >
        <template v-slot:items="props">
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'"><v-btn @click="deleteRow(props)" v-if="props.item.cancle">{{ props.item.cancle }}</v-btn></td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.date }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.su }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.address }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.product_option }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.cnt }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.gong_price }}</td>
            <td :class="props.item['jungbok'] ? `${props.item['jungbok']} text-xs-left` : 'text-xs-left'">{{ props.item.delivery_pay }}</td>

        </template>
        <template slot="footer">
            <td class="text-xs-right" colspan="4">총 금액</td>
            <td class="text-xs-left" colspan="2">{{totalSum()}}</td>
            <td class="text-xs-left">{{sumField('gong_price')}}</td>
           <td class="text-xs-left">{{sumField('delivery_pay')}}</td>
        </template>
        <v-alert v-slot:no-results :value="true" color="error" icon="warning">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </v-data-table>
    </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import xlsx from 'xlsx'
import fileSaver from 'file-saver'
export default {
  data () {
    return {
      terms: {
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10)
      },
      menu: false,
      menu1: false,
      search: '',
      headers: [
        { text: '취소/환불', value: 'cancle' },
        { text: '날짜', value: 'date' },
        { text: '수취인', value: 'su' },
        { text: '주소', value: 'address' },
        { text: '상품명', value: 'product_option' },
        { text: '수량', value: 'cnt' },
        { text: '공급가', value: 'gong_price' },
        { text: '택배비', value: 'delivery_pay' }
      ],
      rowsPage: [{ 'text': '$vuetify.dataIterator.rowsPerPageAll', 'value': -1 }]
    }
  },
  computed: {
    ...mapGetters([
      'getUserData',
      'getCalcuOrderData'
    ]),
    calcuOrderData () {
      if (this.getCalcuOrderData) {
        return this.getCalcuOrderData
      } else {
        return []
      }
    }
  },

  methods: {
    ...mapActions([
      'ac_login',
      'ac_getCalculate'
    ]),
    deleteRow (props) {
      const index = this.getCalcuOrderData.indexOf(props.item)
      this.getCalcuOrderData.splice(index, 1)
    },
    saveToExcel () {
      var worksheet = xlsx.utils.table_to_book(document.getElementById('bb_list_table'))
      var opts = { bookType: 'xlsx', bookSST: false, type: 'binary' }
      var wbout = xlsx.write(worksheet, opts)
      fileSaver.saveAs(new Blob([this.s2ab(wbout)], { type: 'application/octet-stream' }), this.getUserData.nongga_name + '_' + this.startDate + '.xlsx')
    },

    s2ab (s) {
      var buf = new ArrayBuffer(s.length)
      var view = new Uint8Array(buf)
      for (var i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
      return buf
    },
    sumField (key) {
      if (this.getCalcuOrderData) {
        return this.getCalcuOrderData.reduce((a, b) => parseInt(a) + parseInt((b[key] || 0)), 0).toLocaleString('ko-KR')
      } else {
        return 0
      }
    },
    totalSum () {
      if (this.getCalcuOrderData) {
        return (this.reverseNumber(this.sumField('gong_price')) + this.reverseNumber(this.sumField('delivery_pay'))).toLocaleString('ko-KR')
      }
    },
    numberWithCommas (x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    },
    reverseNumber (num) {
      return parseInt(num.replace(/[^\d]+/g, ''))
    }
  }
}
</script>
<style>
.h1 {
  text-align: center;
}
.v-datatable__progress {
  display: none
}
table td.bg{background-color:greenyellow}
</style>
