<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12>
        <v-text-field label="상호" v-model="updateUser.nongga_name" readonly outline></v-text-field>
      </v-flex>
      <v-spacer></v-spacer>
      <v-flex xs12>
        <v-text-field label="아이디" v-model="updateUser.uid" readonly outline></v-text-field>
      </v-flex>
       <v-spacer></v-spacer>
      <v-flex xs12>
        <v-text-field label="비밀번호 재설정" type="password" v-model="updateUser.pw" outline></v-text-field>
      </v-flex>
       <v-spacer></v-spacer>
      <v-flex xs12>
        <v-text-field label="비밀번호 다시입력" type="password" v-model="updateUser.rePw" outline></v-text-field>
      </v-flex>
      <v-flex align-end xs12>
        <v-btn color="success" @click="ac_updateuser(updateUser)" block>저 장</v-btn>
      </v-flex>
       <v-spacer></v-spacer>
    <v-flex xs12 mt-4>
    <template>
      <v-data-table :headers="headers" :items="product">
        <template v-slot:items="props">
          <td class="text-xs-center">{{ props.item.num }}</td>
          <td class="text-xs-center">{{ props.item.name }}</td>
          <td class="text-xs-center">{{ props.item.gongprice }}</td>
        </template>
      </v-data-table>
    </template>
    </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data: () => ({
    headers: [
      { text: '번호', value: 'num', align: 'center' },
      { text: '상품명', value: 'name', align: 'center' },
      { text: '공급가', value: 'gongprice', align: 'center' }
    ]
  }),
  computed: {
    ...mapGetters([
      'getUserData'
    ]),
    product () {
      let json = JSON.parse(this.getUserData.nongga_product)
      for (let i = 0; i < json.length; i++) {
        json[i]['num'] = i + 1
      }
      return json
    },
    updateUser () {
      let userInfo = {}
      userInfo['uid'] = this.getUserData.uid
      userInfo['nongga_name'] = this.getUserData.nongga_name
      userInfo['pw'] = ''
      userInfo['rePw'] = ''
      return userInfo
    }
  },
  methods: {
    ...mapActions([
      'ac_updateuser'
    ])
  }
}
</script>
