<template>
  <v-layout row wrap>
    <v-toolbar class="mb-2" color="indigo darken-5" dark flat>
      <v-toolbar-title>주문 요약 보기</v-toolbar-title>
      <v-btn color="success" align-end @click="uniqName()">주문 요약 보기</v-btn>
    </v-toolbar>
    <v-card v-for="(uniq, index) in uniqName" :key="index">
      <v-list dense>
        <v-list-tile>
          <v-list-tile-content><v-btn @click="getProductResult(uniq.product_name)">{{uniq.product_name}}</v-btn></v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-card>
      <v-toolbar class="mb-2 mt-2" color="indigo darken-5" dark flat>
        <v-toolbar-title>주문 상세 보기</v-toolbar-title>
      </v-toolbar>
    <v-flex xs12>
      <v-btn color="success" align-end @click="saveToExcel(startDate)">엑셀다운</v-btn>
      <v-spacer></v-spacer>
      <v-card xs10 offset-xs1>
      <v-card-title>
        총 <v-chip color="indigo" text-color="white">
      <v-avatar class="indigo darken-4">{{totalSum}}</v-avatar>개
      </v-chip>
        <v-spacer></v-spacer>

        <v-text-field v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="sumPredict"
        :search="search"
        :rows-per-page-items="rowsPage"
        id="bb_list_table"
      >
        <template v-slot:items="props">
          <td class="text-xs-left">{{ props.item.product_name }}</td>
          <td class="text-xs-left">{{ props.item.product_cnt }}</td>
          <td class="text-xs-left">{{ props.item.gong_price }}</td>
        </template>
        <v-alert v-slot:no-results :value="true" color="error" icon="warning">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </v-data-table>
    </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import xlsx from 'xlsx'
import fileSaver from 'file-saver'
import Axios from 'axios'
export default {
  data () {
    return {
      startDate: new Date().toISOString().substr(0, 10),
      menu: false,
      search: '',
      headers: [
        { text: '상품명', value: 'product_name' },
        { text: '수량', value: 'product_cnt' },
        { text: '공급가', value: 'gong_price' }
      ],
      rowsPage: [100, 200, 300, { 'text': '$vuetify.dataIterator.rowsPerPageAll', 'value': -1 }],
      sumPredict: [],
      originalData: [],
      totalSum: 0
    }
  },
  created () {
    Axios.get('https://bagcw.com/auth_patner/predict').then((res) => {
      this.originalData = res.data
    })
  },
  computed: {
    ...mapGetters([
      'getUserData'
    ]),
    uniqName () {
      var predictData22 = []
      if (this.originalData) {
        predictData22 = this.originalData
      }
      var abc = predictData22.reduce((accumulator, current, index) => {
        if (checkIfAlreadyExist(current)) {
          return accumulator
        } else {
          return accumulator.concat([current])
        }
        function checkIfAlreadyExist (currentVal) {
          return accumulator.some((item) => {
            return (item.product_name === currentVal.product_name)
          })
        }
      }, [])
      abc.slice(0, 1)
      console.log(abc)
      return abc
    }

  },

  methods: {
    ...mapActions([
      'ac_login'
    ]),
    saveToExcel () {
      var worksheet = xlsx.utils.table_to_book(document.getElementById('bb_list_table'))
      var opts = { bookType: 'xlsx', bookSST: false, type: 'binary' }
      var wbout = xlsx.write(worksheet, opts)
      fileSaver.saveAs(new Blob([this.s2ab(wbout)], { type: 'application/octet-stream' }), this.getUserData.nongga_name + '_' + this.startDate + '.xlsx')
    },

    s2ab (s) {
      var buf = new ArrayBuffer(s.length)
      var view = new Uint8Array(buf)
      for (var i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
      return buf
    },
    getProductResult (name) {
      this.sumPredict = []
      this.totalSum = 0
      for (let i = 0; i < this.originalData.length; i++) {
        if (name === this.originalData[i].product_name) {
          this.sumPredict.push(this.originalData[i])
          this.totalSum += parseInt(this.originalData[i].product_cnt)
        }
      }
    }
  }
}
</script>
<style>
.h1 {
  text-align: center;
}
.v-datatable__progress {
  display: none
}
</style>
