<template>
  <v-layout row wrap>
    <v-flex xs12>
      <div>
        <v-avatar
          slot="icon"
          color="deep-purple accent-4"
          size="40"
        >
          <v-icon
            color="white"
          >
            lock
          </v-icon>
        </v-avatar>
        제공되는 개인정보는 개인정보보호법에 의거 절대 업무(배송, 고객문의)외 사용을 금지합니다
      </div>
    </v-flex>
    <v-flex xs12 sm6 md4>
      <div>
        <v-menu
          v-model="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="startDate"
                label="발주일"
                prepend-icon="event"
                readonly
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker locale="ko-KR" v-model="startDate" @input="menu = false"></v-date-picker>
          </v-menu>
        <v-btn color="success" @click="getOrder(startDate)">검색</v-btn>
      </div>
    </v-flex>
    <v-flex xs12>
      <v-btn color="success" align-end @click="saveToExcel(startDate)">엑셀다운</v-btn>
      <v-spacer></v-spacer>
      <v-card xs10 offset-xs1>
      <v-card-title>
        <div style="width:100%">
          <span class="title">주문정보</span>
          <br />
          <v-chip
            class="ma-2"
            color="green"
            text-color="white"
            v-for="(sum, key) in summary"
            :key="key"
          >
            {{key}}
            <v-avatar
              right
              class="blue darken-4 mr-0 ml-2"
            >
              {{sum}}
            </v-avatar>

          </v-chip>

          <!--v-select
            :items="templates"
            label="발주양식 선택하기"
            @change="selectTemplate($event)"
          ></v-select-->
        </div>
        <div style="width:100%">
          <v-spacer></v-spacer>
          <v-text-field v-model="search"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </div>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="tableItems"
        :search="search"
        :rows-per-page-items="rowsPage"
        id="bb_list_table"
      >
        <template slot="items" slot-scope="myprops">
        <td v-for="(header, index) in headers" :key="index" :class="myprops.item['jungbok'] ? myprops.item['jungbok'] : ''">
          {{ myprops.item[header.value] ? myprops.item[header.value] === '0' ? '' : myprops.item[header.value] : myprops.item[header.value] !== null ? header.value : ''}}
        </td>
      </template>
        <v-alert v-slot:no-results :value="true" color="error" icon="warning">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </v-data-table>
    </v-card>
    <v-dialog
      v-model="dialog"
    >
      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
          <div style="width:100%">
            <v-btn @click="addRow()" color="yellow" block>열 추가</v-btn>
          </div>
          <div style="width:100%">
            <div>
              <v-text-field label="템플릿명" v-model="templateName"></v-text-field>
            </div>
            <div v-for="(rowInput, index) in rowInputs" :key="index" style="width:20%;display:inline-block;margin-right:10px;">
              <div>
                <v-text-field :label="'제목'+(index+1)" v-model="rowInput.text"></v-text-field>
              </div>
              <div>
                <v-btn class="ma-2" @click="changeForm(index)" x-small tile outlined color="success">
                  폼 변경
                </v-btn>
                <v-select v-if="rowInput.visible" label="내용" :items="selectItems" v-model="rowInput.value"></v-select>
                <v-text-field v-if="!rowInput.visible" label="내용입력" v-model="rowInput.value"></v-text-field>
              </div>
              <div>
                <v-btn @click="removeRowInput(index)" color="red">삭제</v-btn>
              </div>
            </div>
          </div>
        </v-card-title>

        <v-card-text>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="templateSave()"
          >
            저장
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="templageCancle()"
          >
            취소
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from 'vuex'
import xlsx from 'xlsx'
import fileSaver from 'file-saver'
import Vue from 'vue'
import Axios from 'axios'
import UpdateDate from '../plugins/updateDate'
export default {
  data () {
    return {
      rowInputs: [],
      rowInput: {
        text: '',
        value: '',
        visible: true
      },
      templateName: '',
      selectItems: [
        { text: '주문자', value: 'jumunja' },
        { text: '주문자 연락처', value: 'jumun_num' },
        { text: '수취인', value: 'su' },
        { text: '수취인 연락처1', value: 'su_num1' },
        { text: '수취인 연락처2', value: 'su_num2' },
        { text: '우편번호', value: 'zipcode' },
        { text: '주소', value: 'address' },
        { text: '상품명', value: 'product_option' },
        { text: '수량', value: 'cnt' },
        { text: '배송메세지', value: 'message' }
      ],
      dialog: false,
      templates: ['기본양식'],
      startDate: new Date().toISOString().substr(0, 10),
      menu: false,
      search: '',
      headers: [
        { text: '주문자', value: 'jumunja', width: '1%' },
        { text: '주문자 연락처', value: 'jumun_num', width: '1%' },
        { text: '수취인', value: 'su', width: '1%' },
        { text: '수취인 연락처1', value: 'su_num1', width: '1%' },
        { text: '수취인 연락처2', value: 'su_num2', width: '1%' },
        { text: '우편번호', value: 'zipcode', width: '1%' },
        { text: '주소', value: 'address', width: '1%' },
        { text: '상품명(수량)', value: 'product_option', width: '1%' },
        { text: '배송메세지', value: 'message', width: '1%' }
      ],
      rowsPage: [100, 200, 300, { 'text': '$vuetify.dataIterator.rowsPerPageAll', 'value': -1 }],
      tableItems: [],
      summary: {}
    }
  },
  created () {
    // this.templates.push('template1')
    if (this.getUserData.template) {
      this.getCustomeTemplate()
    } else {
      this.getDefoultTemplate()
    }
  },
  computed: {
    ...mapGetters([
      'getUserData'
    ])
  },
  methods: {
    async getOrder (date) {
      const { data } = await Axios.get('https://bagcw.com/auth_patner/order/' + date)
      let updateDate = new UpdateDate()
      let result = updateDate.getUpdateDate(data)
      const jungbok = this.sorting(result).reduce(function (accumulator, current, index) {
        if (current.product_option === null) {
          current.product_option = current.product_name
        }
        current.product_option = `${current.product_option} - ${current.cnt}개`

        if (checkIfAlreadyExist(current)) {
          return accumulator
        } else {
          return accumulator.concat([current])
        }
        function checkIfAlreadyExist (currentVal) {
          return accumulator.some((item) => {
            if (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date) {
              item.product_option = `${item.product_option} / ${currentVal.product_option}`
              item.jungbok = ''
            }
            return (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date)
          })
        }
      }, [])
      this.tableItems = jungbok
      this.summary = this.sorting(this.tableItems).reduce((acc, cur, index) => {
        if (cur.product_option in acc) {
          acc[cur.product_option]++
        } else {
          acc[cur.product_option] = 1
        }
        return acc
      }, {})
    },
    sorting (items) {
      items.sort((a, b) => {
        const nameA = a.product_option
        const nameB = b.product_option
        if (nameA < nameB) {
          return -1
        }
        if (nameA > nameB) {
          return 1
        }
        return 0
      })
      return items
    },
    changeForm (index) {
      if (this.rowInputs[index].visible) {
        this.rowInputs[index].visible = false
      } else {
        this.rowInputs[index].visible = true
      }
    },
    async templateSave () {
      this.rowInputs.push({ name: this.templateName })
      const { data: { success, msg } } = await Axios.post('https://bagcw.com/auth_patner/template', { template: JSON.stringify(this.rowInputs), uid: this.getUserData.num })
      if (success) {
        alert(msg)
        this.templates.push(this.templateName)
        this.dialog = false
        this.rowInputs = []
      } else {
        alert('저장 실패')
      }
    },
    addRow () {
      this.rowInputs.push(Vue.util.extend({}, this.rowInput))
    },
    removeRowInput (index) {
      Vue.delete(this.rowInputs, index)
    },
    createTemplate () {
      this.rowInputs = []
      this.dialog = true
    },
    templageCancle () {
      this.rowInputs = []
      this.dialog = false
    },
    selectTemplate (e) {
      if (e === '기본양식') {
        this.headers = [
          { text: '주문자', value: 'jumunja', width: '1%' },
          { text: '주문자 연락처', value: 'jumun_num', width: '1%' },
          { text: '수취인', value: 'su', width: '1%' },
          { text: '수취인 연락처1', value: 'su_num1', width: '1%' },
          { text: '수취인 연락처2', value: 'su_num2', width: '1%' },
          { text: '우편번호', value: 'zipcode', width: '1%' },
          { text: '주소', value: 'address', width: '1%' },
          { text: '상품명(수량)', value: 'product_option', width: '1%' },
          { text: '배송메세지', value: 'message', width: '1%' }
        ]
      } else {
        if (this.getUserData) {
          const template = JSON.parse(this.getUserData.template)
          template.forEach((item, index) => {
            if (item.name) {
              template.splice(index, 1)
            }
          })
          this.headers = template
        }
      }
    },
    getDefoultTemplate () {
      this.headers = [
        { text: '주문자', value: 'jumunja', width: '1%' },
        { text: '주문자 연락처', value: 'jumun_num', width: '1%' },
        { text: '수취인', value: 'su', width: '1%' },
        { text: '수취인 연락처1', value: 'su_num1', width: '1%' },
        { text: '수취인 연락처2', value: 'su_num2', width: '1%' },
        { text: '우편번호', value: 'zipcode', width: '1%' },
        { text: '주소', value: 'address', width: '1%' },
        { text: '상품명(수량)', value: 'product_option', width: '1%' },
        { text: '배송메세지', value: 'message', width: '1%' }
      ]
    },
    getCustomeTemplate () {
      if (this.getUserData) {
        const template = JSON.parse(this.getUserData.template)
        template.forEach((item, index) => {
          if (item.name) {
            template.splice(index, 1)
          }
        })
        this.headers = template
      }
    },
    saveToExcel () {
      const json = this.tableToJson(document.querySelector('table.v-datatable'))
      var workbook = xlsx.utils.book_new()
      var worksheet = xlsx.utils.json_to_sheet(json)
      xlsx.utils.book_append_sheet(workbook, worksheet, 'Sheet1')
      var opts = { bookType: 'xlsx', bookSST: false, type: 'binary' }
      var wbout = xlsx.write(workbook, opts)
      fileSaver.saveAs(new Blob([this.s2ab(wbout)], { type: 'application/octet-stream' }), this.getUserData.nongga_name + '_' + this.startDate + '.xlsx')
    },
    tableToJson (table) { // 변환 함수
      var data = []
      for (let i = 0; i < table.childNodes[1].rows.length; i++) {
        let json = {}
        for (let k = 0; k < table.childNodes[1].rows[i].cells.length; k++) {
          json[this.headers[k].text] = table.childNodes[1].rows[i].cells[k].innerText
        }
        data.push(json)
      }
      return data
    },
    s2ab (s) {
      var buf = new ArrayBuffer(s.length)
      var view = new Uint8Array(buf)
      for (var i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
      return buf
    }
  }
}
</script>
<style>
.h1 {
  text-align: center;
}
.v-datatable__progress {
  display: none
}
table{table-layout: auto}
table th{word-break:keep-all;text-align: center;text-align:center!important}
table td{text-overflow: ellipsis;overflow:hidden;white-space:nowrap}
table td.bg{background-color:greenyellow}
</style>
