<template>
  <v-layout row wrap>
    <v-flex xs12 sm6 md4>
      <v-menu
        v-model="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="startDate"
            label="발주일"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker locale="ko-KR" v-model="startDate" @input="menu = false"></v-date-picker>
      </v-menu>

    </v-flex>
    <v-btn color="success" @click="getOrder(startDate)">검색</v-btn>
    <v-flex xs12>
      <span class="title">주문정보</span>
      <br />
      <v-chip
        class="ma-2"
        color="green"
        text-color="white"
        v-for="(sum, key) in summary"
        :key="key"
      >
        {{key}}
        <v-avatar
          right
          class="green darken-4 mr-0 ml-2"
        >
          {{sum}}
        </v-avatar>

      </v-chip>
      <v-data-iterator
      :items="tableItems"
      :rows-per-page-items="rowsPage"
      content-tag="v-layout"
      row
      wrap
    >
      <template v-slot:item="props">
        <v-flex
          xs12
          sm6
          md4
          lg3
        >
          <v-card :class="props.item['jungbok'] ? props.item['jungbok'] : ''">
            <v-card-title class="blue lighten-5"><h4>{{ props.index + 1 }}</h4></v-card-title>
            <v-divider></v-divider>
            <v-list dense>
              <v-list-tile>
                <v-list-tile-content class="align-start">날짜 : {{ props.item.date }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">주문자 : {{ props.item.jumunja }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">수취인 : {{ props.item.su }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">수취인 연락처1 : {{ props.item.su_num1 }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">수취인 연락처2 : {{ props.item.su_num2 }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">주소 : {{ props.item.address }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">상품 : {{ props.item.product_option }}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="align-start">배송메세지 : {{ props.item.message }}</v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-card>
        </v-flex>
      </template>
    </v-data-iterator>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from 'vuex'
import Axios from 'axios'
import UpdateDate from '../plugins/updateDate'
export default {
  data () {
    return {
      startDate: new Date().toISOString().substr(0, 10),
      menu: false,
      search: '',
      rowsPage: [{ 'text': '$vuetify.dataIterator.rowsPerPageAll', 'value': -1 }],
      tableItems: [],
      summary: {}
    }
  },

  computed: {
    ...mapGetters([
      'getUserData'
    ])
  },

  methods: {
    async getOrder (date) {
      const { data } = await Axios.get('https://bagcw.com/auth_patner/order/' + date)
      let updateDate = new UpdateDate()
      let result = updateDate.getUpdateDate(data)
      const jungbok = result.reduce(function (accumulator, current, index) {
        current.product_option = `${current.product_option} - ${current.cnt}개`
        if (checkIfAlreadyExist(current)) {
          return accumulator
        } else {
          return accumulator.concat([current])
        }
        function checkIfAlreadyExist (currentVal) {
          return accumulator.some((item) => {
            if (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date) {
              item.product_option = `${item.product_option} / ${currentVal.product_option}`
              item.jungbok = ''
            }
            return (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date)
          })
        }
      }, [])
      this.tableItems = jungbok
      this.summary = this.tableItems.reduce((acc, cur, index) => {
        if (cur.product_option in acc) {
          acc[cur.product_option]++
        } else {
          acc[cur.product_option] = 1
        }
        return acc
      }, {})
    },
    sumPojang () {
      this.tableItems = this.sorting(this.tableItems).reduce(function (accumulator, current, index) {
        if (checkIfAlreadyExist(current)) {
          return accumulator
        } else {
          return accumulator.concat([current])
        }
        function checkIfAlreadyExist (currentVal) {
          return accumulator.some((item) => {
            if (item.su === currentVal.su && item.address === currentVal.address) {
              item.product_option = `${item.product_option} / ${currentVal.product_option}`
              item.jungbok = ''
            }
            return (item.su === currentVal.su && item.address === currentVal.address)
          })
        }
      }, [])
    },
    sorting (items) {
      items.sort((a, b) => {
        const nameA = a.product_option
        const nameB = b.product_option
        if (nameA < nameB) {
          return -1
        }
        if (nameA > nameB) {
          return 1
        }

        // 이름이 같을 경우
        return 0
      })
      return items
    }
  }
}
</script>
<style>
.h1 {
  text-align: center;
}
.v-datatable__progress {
  display: none
}
